import React from 'react';
import axios from 'axios'
import ReactDom from 'react-dom';
import './App.css';

import Form from 'react-bootstrap/Form'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'


class PumpSettings extends React.Component{
    constructor(props){
        super(props)
        this.state ={
            option:'off',
            forms:1,
            scheduleOption:'',
            schedule:[{time:'',days:'',duration:''}],
            selectOption:'',
            threshold:''
        }

    }
    componentWillReceiveProps(nextProps) {
        switch(nextProps.status.type){
            case 'off':
                this.setState({option:nextProps.status.type}); 
                break;
            case 'auto':
                this.setState({option:nextProps.status.type,threshold:nextProps.status.threshold})
                break;       
            case 'schedule':
            case 'scheduleday':
                this.setState({option:nextProps.status.type,forms:nextProps.status.schedule.length,schedule:nextProps.status.schedule,scheduleOption:nextProps.status.type})
                break;        
        }
      }
    scheduleHTML(scheduleOption){
        console.log(this.state.forms, this.state.schedule)
        switch(scheduleOption){
            default:
                return(
                    <Form.Group>
                    {[...Array(this.state.forms).keys()].map(i => {
                        if(i == 0){
                            return(                            
                                <Form.Row key={i}>
                                    <Form.Label column="sm" lg={0.5}>Time</Form.Label>
                                    <Col>
                                        <Form.Control defaultValue={this.state.schedule[i].time} id={'time-'+ this.props.id + '-' + i} type="time"></Form.Control>
                                    </Col>    
                                    <Form.Label column="sm" lg={0.5}>Duration</Form.Label>
                                    <Col>
                                    <Form.Control defaultValue={this.state.schedule[i].duration} id={'duration-'+ this.props.id + '-' + i} type="number"></Form.Control>
                                    </Col>                           
                                </Form.Row>)
                        } else {
                            return(                            
                                <Form.Row key={i} style={{paddingTop:'5px'}}>
                                    <Form.Label column="sm" lg={0.5}>Time</Form.Label>
                                    <Col>
                                        <Form.Control defaultValue={this.state.schedule[i].time} id={'time-'+ this.props.id + '-' + i} type="time"></Form.Control>
                                    </Col>    
                                    <Form.Label column="sm" lg={0.5}>Duration</Form.Label>
                                    <Col>
                                    <Form.Control defaultValue={this.state.schedule[i].duration} id={'duration-'+ this.props.id + '-' + i} type="number"></Form.Control>
                                    </Col>                           
                                </Form.Row>)                               
                        }

                    })}
                    </Form.Group>
                )
            case 'scheduleday':
                return(
                    <div>
                        <Form.Group>
                        {[...Array(this.state.forms).keys()].map(i => {
                            if(i == 0){
                                return(                         
                                    <Form.Row key={i}>
                                        <Col>
                                            <Form.Control defaultValue={this.state.schedule[i].days} id={'scheduleday-' + this.props.id + '-' + i} as="select">
                                                <option>Monday</option>
                                                <option>Tuesday</option>
                                                <option>Wednesday</option>
                                                <option>Thursday</option>
                                                <option>Friday</option>
                                                <option>Saturday</option>
                                                <option>Sunday</option>
                                            </Form.Control>
                                        </Col>
                                        <Form.Label column="sm" lg={0.5}>Time</Form.Label>
                                        <Col>
                                            <Form.Control defaultValue={this.state.schedule[i].time} id={'time-'+ this.props.id + '-' + i} type="time"></Form.Control>
                                        </Col>    
                                        <Form.Label column="sm" lg={0.5}>Duration</Form.Label>
                                        <Col>
                                        <Form.Control defaultValue={this.state.schedule[i].duration} id={'duration-'+ this.props.id + '-' + i} type="number"></Form.Control>
                                        </Col>                           
                                    </Form.Row>)
                            } else {
                                return(                            
                                    <Form.Row key={i} style={{paddingTop:'5px'}}>
                                        <Col>
                                            <Form.Control defaultValue={this.state.schedule[i].days} id={'scheduleday-' + this.props.id + '-' + i} as="select">
                                                <option>Monday</option>
                                                <option>Tuesday</option>
                                                <option>Wednesday</option>
                                                <option>Thursday</option>
                                                <option>Friday</option>
                                                <option>Saturday</option>
                                                <option>Sunday</option>
                                            </Form.Control>
                                        </Col>
                                        <Form.Label column="sm" lg={0.5}>Time</Form.Label>
                                        <Col>
                                            <Form.Control defaultValue={this.state.schedule[i].time} id={'time-'+ this.props.id + '-' + i} type="time"></Form.Control>
                                        </Col>    
                                        <Form.Label column="sm" lg={0.5}>Duration</Form.Label>
                                        <Col>
                                        <Form.Control defaultValue={this.state.schedule[i].duration} id={'duration-'+ this.props.id + '-' + i} type="number"></Form.Control>
                                        </Col>                           
                                    </Form.Row>)                               
                            }

                        })}
                        </Form.Group>
                    </div>                    
                )
        }
    }
    settingsHTML(option, scheduleOption, threshold){
        console.log('running settings',option)
        switch(option){
            case 'off':
                return (                        
                <Row>
                    <Col>
                        <Button onClick={(e) => this.saveSettings()} style={{float:'right'}}>Save</Button>
                    </Col>
                </Row>)
            case 'auto':
                return(
                    <div key={this.props.id}>
                        <Form.Group>
                            <Form.Row>
                                <Form.Label column="sm" lg={2}>Water Threshold</Form.Label>
                                <Col>
                                    <Form.Control defaultValue={threshold} id={'threshold-'+this.props.id} type="number"></Form.Control>
                                </Col>
                            </Form.Row>
                        </Form.Group>
                        <Row>
                            <Col>
                                <Button onClick={(e) => this.saveSettings()} style={{float:'right'}}>Save</Button>
                            </Col>
                        </Row>
                    </div>
                )
            case 'schedule':
            case 'scheduleday':
                return(
                    <div key={this.props.id}>
                        <Form.Group>
                            <Form.Control defaultValue={scheduleOption} onChange={(e) => this.switchScheduleOption()} id="scheduleOption" as="select">
                                <option value="schedule">Daily</option>
                                <option value="scheduleday">Specify Days</option>
                            </Form.Control>
                        </Form.Group>
                        {this.scheduleHTML(scheduleOption)}
                        <Row>
                            <Col>
                                <Button onClick={(e) => this.saveSettings()} size="sm" style={{float:'right'}}>Save</Button>
                                <Button onClick={e => this.addForm()} size="sm" style={{float:'right',paddingRight:'17px',paddingLeft:'17px', marginRight:'10px'}}>+</Button>
                            </Col>
                        </Row>
                    </div>
                )
            default:
                return <div>null</div>
        }
    }
    switchScheduleOption(){
        this.setState({scheduleOption:document.getElementById('scheduleOption').value})
    }
    switchOption(){
        this.setState({option:document.getElementById('pumpSettingsOption-' + this.props.id).value})
    }
    addForm(){
        var newSchedule = this.state.schedule.slice()
        newSchedule.push({time:'',days:'',duration:''})
        this.setState({ schedule:newSchedule, forms:this.state.forms + 1})
    }
    saveSettings(){
        
        var ngrokaddr = 'https://19d7bd9d.ngrok.io'

        if(localStorage.ngrok){

            ngrokaddr = localStorage.ngrok
        }

        var body = {}

        if(this.state.option == 'schedule' || this.state.option == 'scheduleday'){
            var days = ''
            var times = ''
            var durations = ''
            var nextElement = ''
            var scheduleElements = null

            scheduleElements = document.evaluate("//input[contains(@id,'time-" + this.props.id + "')]",document, null,XPathResult.ORDERED_NODE_ITERATOR_TYPE, null)
            while(nextElement !== null){
                nextElement = scheduleElements.iterateNext()
                if(nextElement){
                    times = times + nextElement.value + ';'
                }
            }
            
            nextElement = ''
            scheduleElements = document.evaluate("//input[contains(@id,'duration-" + this.props.id + "')]",document, null,XPathResult.ORDERED_NODE_ITERATOR_TYPE, null)
            while(nextElement !== null){
                nextElement = scheduleElements.iterateNext()
                if(nextElement){
                    durations = durations + nextElement.value + ';'
                }
            }
            body.type = 'schedule'

            if(this.state.scheduleOption == 'scheduleday'){
                nextElement = ''
                scheduleElements = document.evaluate("//select[contains(@id,'scheduleday-" + this.props.id + "')]",document, null,XPathResult.ORDERED_NODE_ITERATOR_TYPE, null)
                while(nextElement !== null){
                    nextElement = scheduleElements.iterateNext()
                    if(nextElement){
                        days = days + nextElement.value + ';'
                    }
                }
                days = days.substring(0, days.length-1)
                body.days = days
                body.type = 'scheduleday'
            }

            times = times.substring(0, times.length-1)
            durations = durations.substring(0, durations.length-1)

            body.times = times
            body.durations = durations
        }

        switch(this.state.option){
            case 'off':
                body = {pumpid:this.props.id,type:'off',pass:document.getElementById('postpass').value,pass:document.getElementById('postpass').value}
                break;
            case 'auto':
                body = {pumpid:this.props.id,type:'auto',threshold:parseInt(document.getElementById('threshold-'+this.props.id).value), pass:document.getElementById('postpass').value}
                break;
            case 'schedule':
            case 'scheduleday':
                body.pumpid = this.props.id
                body.pass = document.getElementById('postpass').value
                break;
            default:
                body = {}
        }   

        console.log(body)

		axios.post(ngrokaddr + '/updatestatus', body, {crossdomain:true}).then((res) =>{
			console.log(res.data)
		})
    }
    optionSelectHTML(selectOption){
    return(
    <Form>
        <Form.Group>
            <Form.Control id={"pumpSettingsOption-" + this.props.id} defaultValue={selectOption.replace('day','')} onChange={(e) => this.switchOption()} as="select">
                <option value="off">Off</option>
                <option value="auto">Auto Water</option>
                <option value="schedule">Schedule</option>
            </Form.Control>
        </Form.Group>
    </Form>)
    }
	render(){
	  return (
		<div className="pumpsettings" key={this.props.id}>
            <div style={{paddingBottom:'10px'}}>Settings</div>
            {this.optionSelectHTML(this.state.option)}
            {this.settingsHTML(this.state.option, this.state.scheduleOption, this.state.threshold)}
		</div>
	  );
	}
}

export default PumpSettings;
